import React from "react";

class Player extends React.Component {
	render() {
		let player      = this.props.player;
		let player_type = (player.name === "YOU") ? "active" : "other";
		let job         = player.Job.toUpperCase() || "LMB";
		let columns     = [];

		for (let title in this.props.columns) {
			let key   = this.props.columns[title];
			let value = (typeof key === "function") ? key(player, this.props.players) : player[key];

			if (String(Number(value)) === String(value)) {
				value = (+value).toLocaleString(undefined, { maximumFractionDigits: 2 });
			}

			columns.push(
				<div className="column">{value}</div>
			);
		}

		return (
			<div className={"row player " + player_type} onClick={this.props.onClick}>
				<div className="column"><img src={"img/icons/jobs/" + job + ".png"}></img></div>
				<div className="column">{player.name}</div>
				{columns}
			</div>
		);
	}
}

export default Player;